import React from 'react';
import styled from '@emotion/styled';

interface IMenuItem {
  icon: React.ReactNode;
  name: string;
  onClick?: (event: any) => void;
}

const MenuItem: React.FC<IMenuItem> = ({ icon, name, onClick }) => {
  return (
    <Container onClick={onClick}>
      <Icon>{icon}</Icon>
      <Name>{name}</Name>
    </Container>
  );
};

export default MenuItem;

const Container = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;

  &:first-of-type {
    margin-bottom: 26px;
  }
`;

const Icon = styled.div`
  width: 19px;
  height: 19px;
  margin-right: 11px;
`;

const Name = styled.span`
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  color: #ffffff;
`;
