import React, { useState } from 'react';
import css from '@emotion/css';
import styled from '@emotion/styled';

import MenuItem from './MenuItem';
import Input from '../Input';
import Modal from '../Modal';

import Dots from '../../assets/icons/dots.svg';
import Trash from '../../assets/icons/trash.svg';
import Rename from '../../assets/icons/rename.svg';
import { api } from '../../utils/api';
import { useHistory } from 'react-router';

interface IPlaylistItemContainer {
  selected?: boolean;
}

interface IPlaylistItem extends IPlaylistItemContainer {
  id: number;
  className?: string;
  songs: number;
  name: string;
  onClick?: () => void;
}

const PlaylistItem: React.FC<IPlaylistItem> = ({
  id,
  className = '',
  songs,
  name,
  selected = false,
  onClick,
}) => {
  const history = useHistory();
  const [changedName, setChangedName] = useState<string>(name);
  const [isMenuOpen, setIsMenuOpen] = useState<boolean>(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState<boolean>(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState<boolean>(false);

  const eventPropagationHandler = (event: any, callback: () => void) => {
    let e = event || window.event;
    e.cancelBubble = true;
    if (e.stopPropagation) e.stopPropagation();
    callback();
  };

  const onClickRightIcon = (event: any) =>
    eventPropagationHandler(event, () => setIsMenuOpen(!isMenuOpen));

  const onClickTrash = (event: any) =>
    eventPropagationHandler(event, () => setIsDeleteModalOpen(true));

  const onClickRename = (event: any) =>
    eventPropagationHandler(event, () => setIsEditModalOpen(true));

  const changePlaylistName = async () => {
    await api.post('/playlist/rename', {
      playlist_id: id,
      playlist_name: changedName,
    });
    setIsEditModalOpen(false);
    window.location.reload();
  };

  const removePlaylist = async () => {
    await api.delete('/playlist/delete', { params: { playlist_id: id } });
    setIsDeleteModalOpen(false);
    if (window.location.href.includes(id.toString())) {
      history.goBack();
    } else {
      window.location.reload();
    }
  };

  return (
    <>
      <Container
        className={className}
        selected={isMenuOpen || selected}
        onClick={onClick}
        style={{ cursor: onClick ? 'pointer' : 'auto' }}
      >
        <LeftWrapper>
          <SongCount>{songs}</SongCount>
          <PlaylistName>{name}</PlaylistName>
        </LeftWrapper>
        <RightIcon onClick={onClickRightIcon}>
          <Dots />
        </RightIcon>
        {isMenuOpen && (
          <MenuContainer>
            <MenuItem icon={<Trash />} name="삭제하기" onClick={onClickTrash} />
            <MenuItem
              icon={<Rename />}
              name="이름변경"
              onClick={onClickRename}
            />
          </MenuContainer>
        )}
      </Container>
      <Modal
        open={isEditModalOpen}
        onClose={() => setIsEditModalOpen(false)}
        firstButtonLabel="확인"
        onClickFirstButton={changePlaylistName}
        secondButtonLabel="취소"
        onClickSecondButton={() => setIsEditModalOpen(false)}
        title="PLAYLIST 이름 변경"
        content={
          <Input
            icon="edit"
            placeholder="새로운 이름을 입력하세요."
            value={changedName}
            onChange={(e: any) => setChangedName(e.target.value)}
          />
        }
      />
      {isDeleteModalOpen && (
        <Modal
          open={isDeleteModalOpen}
          onClose={() => setIsDeleteModalOpen(false)}
          firstButtonLabel="확인"
          onClickFirstButton={removePlaylist}
          secondButtonLabel="취소"
          onClickSecondButton={() => setIsDeleteModalOpen(false)}
          title="PLAYLIST 삭제"
          content={
            <>
              <strong>{`[${name}]`}</strong>
              <br />
              해당 플레이리스트를 삭제하시겠습니까?
            </>
          }
        />
      )}
    </>
  );
};

export default PlaylistItem;

const Container = styled.div<IPlaylistItemContainer>`
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 8px;
  background-color: #ffffff;
  padding: 20px 30px;
  margin-bottom: 10px;
  position: relative;

  html[color-mode='dark'] & {
    background-color: rgba(27, 31, 40, 0.96);
  }

  @media screen and (max-width: 360px) {
    padding: 20px 25px;
    margin-bottom: 5px;
  }

  ${({ selected = false }) =>
    selected &&
    css`
      border: solid 1px #295396;
    `};
`;

const LeftWrapper = styled.div`
  display: flex;
  margin-left: 10px;
  align-items: center;
`;

const SongCount = styled.span`
  border-radius: 10px;
  background-color: #295396;
  padding: 4px;
  font-size: 13px;
  font-weight: bold;
  line-height: 0.92;
  color: #fffefe;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
`;

const PlaylistName = styled.span`
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  color: #1e1e1e;

  html[color-mode='dark'] & {
    color: white;
  }
`;

const RightIcon = styled.div`
  width: 18px;
  height: 19px;
  cursor: pointer;
`;

const MenuContainer = styled.div`
  width: 137px;
  height: 113px;
  background-color: #295396;
  position: absolute;
  top: 59px;
  right: -1px;
  z-index: 997;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 25px;
  padding-left: 26px;
  padding-bottom: 28px;
`;
