import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import styled from '@emotion/styled';

import TextHeader from '../components/TextHeader';
import {
  StyledServiceWrapper,
  TopContents,
  BottomContainer,
  BottomContents,
} from '../components/SectionPage';
import PlaylistItem from '../components/PlaylistItem';
import { IPlalist } from '../types';
import { api } from '../utils/api';
import Modal from '../components/Modal';
import { makeNewPlaylist, getPlaylist } from '../utils/playlist';
import Input from '../components/Input';

const Playlist = () => {
  const history = useHistory();
  const [data, setData] = useState<Array<IPlalist>>();

  const [isOpenPlaylistModal, setIsOpenPlaylistModal] = useState(false);
  const [newPlaylistName, setNewPlaylistName] = useState<string>('');

  useEffect(() => {
    const fetchData = async () => {
      const {
        data: {
          data: { playlists },
        },
      } = await api.get('/playlist/index');
      setData(playlists);
    };
    fetchData();
  }, []);

  return (
    <>
      <StyledServiceWrapper>
        <TopContents>
          <TextHeader>PLAYLIST</TextHeader>
        </TopContents>
        <BottomContainer>
          <StyledBottomContents>
            <PlaylistItemList>
              {data?.map((playlist) => (
                <PlaylistItem
                  key={`playlist-${playlist.id}`}
                  id={playlist.id}
                  onClick={() => history.push(`/playlist/${playlist.id}`)}
                  name={playlist.name}
                  songs={playlist.total_count}
                />
              ))}
              <CreateButton onClick={() => setIsOpenPlaylistModal(true)}>
                + NEW PLAYLIST
              </CreateButton>
            </PlaylistItemList>
          </StyledBottomContents>
        </BottomContainer>
      </StyledServiceWrapper>{' '}
      {isOpenPlaylistModal && (
        <Modal
          open={isOpenPlaylistModal}
          onClose={() => setIsOpenPlaylistModal(false)}
          title="NEW PLAYLIST"
          firstButtonLabel="확인"
          onClickFirstButton={async () => {
            await makeNewPlaylist(newPlaylistName);
            const playlists = await getPlaylist();
            setData(playlists);
            setIsOpenPlaylistModal(false);
          }}
          secondButtonLabel="취소"
          onClickSecondButton={() => setIsOpenPlaylistModal(false)}
          content={
            <>
              <StyledInput
                icon="edit"
                placeholder="PLAYLIST 이름을 입력해주세요"
                value={newPlaylistName}
                onChange={(e) => setNewPlaylistName(e.target.value)}
              />
            </>
          }
        />
      )}
    </>
  );
};

export default Playlist;

const StyledBottomContents = styled(BottomContents)`
  padding-top: 80px;

  @media screen and (max-width: 960px) {
    padding-top: 49px;
  }

  @media screen and (max-width: 360px) {
    padding-top: 30px;
  }
`;

const PlaylistItemList = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`;

const CreateButton = styled.button`
  width: 100%;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.05);
  padding: 21px 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  color: #1e1e1e;
  border: none;
  html[color-mode='dark'] & {
    color: white;
    background-color: #252629;
  }
`;

const StyledInput = styled(Input)`
  margin-top: 36px;
  html[color-mode='dark'] & {
    background-color: #2e3137;
  }
`;
